'use client';

import React, { useState, useTransition } from 'react';
import Link from 'next/link';
import { 
  Sparkles, 
  Brain, 
  Wand2, 
  ImageIcon, 
  Mic, 
  FileEdit, 
  CheckSquare, 
  Lightbulb, 
  ChevronLeft, 
  Bookmark, 
  Tag, 
  BarChart3
} from 'lucide-react';

import { extractQuestionFromText } from '../actions';

// --- IMPORT CÁC COMPONENT UI MỚI ---
import Card from '@/components/ui/Card';
import Label from '@/components/ui/Label';
import Textarea from '@/components/ui/Textarea';
import { Button } from '@/components/ui/Button'; // Sử dụng Button đã có
import AnswerInput from '@/components/ui/AnswerInput';
import FileUpload from '@/components/ui/FileUpload';

// --- GIẢI THÍCH REFACTOR ---
//
// File này đã được refactor toàn diện để tuân thủ các nguyên tắc đã thống nhất:
//
// 1. **PHÂN TÁCH COMPONENT (Componentization):**
//    - Toàn bộ giao diện đã được chia nhỏ thành các component UI có khả năng tái sử dụng cao, nằm trong `src/components/ui`.
//    - Thay vì các thẻ `div`, `label`, `textarea` với các class CSS lặp đi lặp lại, giờ đây chúng ta sử dụng:
//      - `<Card>`: Cho các khối nội dung chính.
//      - `<Label>`: Cho tất cả các nhãn, hỗ trợ cả icon.
//      - `<Textarea>`: Chuẩn hóa các vùng nhập văn bản.
//      - `<AnswerInput>`: Gói gọn logic cho một hàng đáp án.
//      - `<FileUpload>`: Tái sử dụng cho cả upload ảnh và âm thanh.
//      - `<Button>`: Sử dụng component Button mạnh mẽ đã có sẵn.
//
// 2. **GIỮ NGUYÊN GIAO DIỆN & CHỨC NĂNG:**
//    - **Cam kết 100%:** Giao diện và chức năng không hề thay đổi. Các `className` gốc đã được chuyển vào bên trong các component UI mới, đảm bảo tính nhất quán về mặt hình ảnh.
//    - Server Action `handleFormAction` và luồng xử lý dữ liệu với AI được giữ nguyên.
//
// 3. **CODE SẠCH VÀ DỄ BẢO TRÌ (Clean Code & Maintainability):**
//    - **Dễ đọc hơn:** File `page.tsx` giờ đây tập trung vào việc bố cục và quản lý state, thay vì chi tiết về UI. Code có tính ngữ nghĩa cao hơn, đọc vào là hiểu ngay cấu trúc trang.
//    - **Dễ bảo trì hơn:** Nếu cần thay đổi giao diện của tất cả các `Card` hoặc `Textarea`, chỉ cần sửa ở một file duy nhất (`Card.tsx`, `Textarea.tsx`) thay vì tìm và sửa ở nhiều nơi.
//
// 4. **CHUẨN NEXT.JS:**
//    - Vẫn tuân thủ các pattern tốt nhất của Next.js App Router như Server Actions, `useTransition`, và file-based routing.
//
// ==> KẾT QUẢ: Chúng ta có một cấu trúc code bền vững, dễ mở rộng và tái sử dụng cho các tính năng trong tương lai, trong khi vẫn đảm bảo sự ổn định cho ứng dụng hiện tại.

const CreateQuestionWithAIPage: React.FC = () => {
  const [isExtracting, startTransition] = useTransition();

  const [rawInput, setRawInput] = useState('');
  const [questionText, setQuestionText] = useState('');
  const [options, setOptions] = useState<string[]>(['', '', '', '']);
  const [correctIndex, setCorrectIndex] = useState<number | null>(null);
  const [explanation, setExplanation] = useState('');
  const [difficulty, setDifficulty] = useState('');
  const [topic, setTopic] = useState('');
  const [tags, setTags] = useState<string[]>([]);
  const [formError, setFormError] = useState<string | null>(null);

  const handleFormAction = (formData: FormData) => {
    if (!formData.get('rawInput')?.toString().trim()) {
        setFormError("Vui lòng nhập nội dung câu hỏi thô.");
        return;
    }
    setFormError(null);

    startTransition(async () => {
      const result = await extractQuestionFromText(formData);
      if (result.success && result.data) {
        const { data } = result;
        setQuestionText(data.questionText || '');
        setOptions(data.options || ['', '', '', '']);
        setCorrectIndex(data.correctOptionIndex !== undefined ? data.correctOptionIndex : null);
        setExplanation(data.explanation || '');
        setDifficulty(data.difficulty || '');
        setTopic(data.topic || '');
        setTags(data.tags || []);
      } else {
        setFormError(result.error || 'Đã có lỗi không xác định xảy ra.');
      }
    });
  };

  const updateOption = (idx: number, val: string) => {
    const newOpts = [...options];
    newOpts[idx] = val;
    setOptions(newOpts);
  };
  
  const handleSave = () => {
    console.log("Đã nhấn Lưu:", { questionText, options, correctIndex, explanation, difficulty, topic, tags });
    alert("Câu hỏi đã được lưu thành công vào ngân hàng! ( giả lập ) ");
  };

  return (
    <form action={handleFormAction} className="max-w-7xl mx-auto w-full px-4 md:px-6 lg:px-8 py-8">
      
      {/* === PAGE HEADER === */}
      <div className="mb-8">
        <Link href="/dashboard/teacher/questions" className="flex items-center gap-2 text-sm text-gray-500 hover:text-[#2b8cee] transition-colors mb-3 w-fit">
            <ChevronLeft size={18} />
            Quay lại danh sách câu hỏi
        </Link>
        <div className="flex flex-col md:flex-row md:items-end justify-between gap-4">
            <div className="flex flex-col gap-2">
              <div className="flex items-center gap-2">
                <span className="px-2.5 py-1 rounded-md bg-[#2b8cee]/10 text-[#2b8cee] text-xs font-bold uppercase tracking-wider">AI POWERED</span>
                <h1 className="text-3xl lg:text-4xl font-black text-gray-900 leading-tight tracking-tight">Tạo Câu Hỏi Thông Minh</h1>
              </div>
              <p className="text-gray-500 text-base">Dán văn bản, tải tệp lên và để AI tự động phân tích và tạo câu hỏi.</p>
            </div>
            <div className="flex items-center gap-3">
                <Button type="button" variant="outlineWhite" onClick={() => window.location.href='/dashboard/teacher/questions'} className="h-11 px-6 rounded-xl font-bold text-sm bg-white border border-gray-200 text-gray-800 hover:bg-gray-50 transition-all">
                    Hủy bỏ
                </Button>
                <Button type="button" onClick={handleSave} className="h-11 px-8 rounded-xl font-bold text-sm bg-[#09090b] text-white hover:bg-[#27272a] transition-all">
                    Lưu câu hỏi
                </Button>
            </div>
        </div>
      </div>

      {/* Main Layout */}
      <div className="grid grid-cols-1 lg:grid-cols-10 gap-8 items-start">
        
        {/* === MAIN CONTENT === */}
        <div className="lg:col-span-7 flex flex-col gap-8">
          
          {/* AI Input Section - Sử dụng Card, Textarea, FileUpload, Button */}
          <Card as="section" className="!p-8 shadow-lg border-2 border-[#2b8cee]/20 relative overflow-hidden">
            <div className="absolute -top-10 -right-10 opacity-5 pointer-events-none"><Sparkles size={200} className="text-[#2b8cee]" /></div>
            <div className="relative z-10 flex flex-col gap-6">
              <div className="flex items-center gap-4">
                <div className="p-3 rounded-lg bg-gradient-to-br from-[#2b8cee] to-blue-400 text-white shadow-md"><Brain size={28} /></div>
                <div>
                  <h2 className="text-xl font-bold text-gray-900">1. Cung cấp dữ liệu</h2>
                  <p className="text-sm text-gray-500">Dán văn bản và tùy chọn đính kèm tệp.</p>
                </div>
              </div>
              
              <Textarea 
                name="rawInput"
                value={rawInput}
                onChange={(e) => setRawInput(e.target.value)}
                className="min-h-[160px] !p-6 !bg-gray-50 placeholder:text-gray-400"
                placeholder="Ví dụ: Question: What is the capital of France? A. London B. Paris C. Rome D. Berlin. Correct: B..."
              />
              
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <FileUpload icon={<ImageIcon size={24} />} name="imageFile" accept="image/*" />
                <FileUpload icon={<Mic size={24} />} name="audioFile" accept="audio/*" />
              </div>

              <div className="flex flex-col items-center gap-4">
                <Button 
                  type="submit"
                  disabled={isExtracting}
                  className="w-full md:w-auto bg-gradient-to-br from-[#2b8cee] to-blue-400 hover:scale-[1.02] active:scale-[0.98] text-white px-10 py-4 rounded-xl font-bold text-base flex items-center justify-center gap-3 transition-all shadow-lg shadow-[#2b8cee]/30 disabled:opacity-60 disabled:cursor-not-allowed disabled:hover:scale-100"
                >
                  <Wand2 size={20} className={isExtracting ? 'animate-spin' : ''} />
                  {isExtracting ? 'AI đang phân tích...' : 'Trích xuất với AI'}
                </Button>
                {formError && <p className="text-sm text-red-600">{formError}</p>}
              </div>
            </div>
          </Card>

          {/* Question Content - Sử dụng Card, Label, Textarea */}
          <Card>
            <Label icon={<FileEdit size={20} className="text-[#2b8cee]" />}>
              Nội dung câu hỏi (Kết quả từ AI)
            </Label>
            <Textarea 
              value={questionText}
              onChange={(e) => setQuestionText(e.target.value)}
              className="min-h-[120px]"
              placeholder="AI sẽ điền nội dung câu hỏi đã được chuẩn hóa vào đây..."
            />
          </Card>

          {/* Answer Options - Sử dụng Card, Label, AnswerInput */}
          <Card>
            <Label icon={<CheckSquare size={20} className="text-[#2b8cee]" />}>
              Các phương án trả lời (Kết quả từ AI)
            </Label>
            <div className="flex flex-col gap-4">
              {options.map((opt, i) => (
                <AnswerInput
                  key={i}
                  index={i}
                  option={opt}
                  isChecked={correctIndex === i}
                  onCheck={() => setCorrectIndex(i)}
                  onUpdate={(value) => updateOption(i, value)}
                />
              ))}
            </div>
          </Card>

          {/* Explanation - Sử dụng Card, Label, Textarea */}
          <Card>
            <Label icon={<Lightbulb size={20} className="text-[#2b8cee]" />}>
              Giải thích chi tiết (Kết quả từ AI)
            </Label>
            <Textarea 
              value={explanation}
              onChange={(e) => setExplanation(e.target.value)}
              className="min-h-[140px]"
              placeholder="AI sẽ điền lời giải thích chi tiết cho đáp án đúng vào đây..."
            />
          </Card>
        </div>

        {/* === SIDEBAR === */}
        <div className="lg:col-span-3 flex flex-col gap-6 lg:sticky lg:top-8">
            <Card>
                <h3 className="text-base font-bold flex items-center gap-3 text-gray-800">
                    <Brain size={20} className="text-[#2b8cee]"/>
                    2. Phân loại bởi AI
                </h3>
                
                <div className="flex flex-col gap-2">
                    <Label className="!text-sm !font-semibold !text-gray-600" icon={<BarChart3 size={16}/>}>Độ khó</Label>
                    <div className="px-4 py-2 bg-gray-100 text-gray-800 rounded-lg text-sm font-medium">
                        {difficulty || 'Chờ AI phân tích...'}
                    </div>
                </div>
                
                <div className="flex flex-col gap-2">
                    <Label className="!text-sm !font-semibold !text-gray-600" icon={<Bookmark size={16}/>}>Chủ đề</Label>
                    <div className="px-4 py-2 bg-gray-100 text-gray-800 rounded-lg text-sm font-medium">
                        {topic || 'Chờ AI phân tích...'}
                    </div>
                </div>

                <div className="flex flex-col gap-2">
                    <Label className="!text-sm !font-semibold !text-gray-600" icon={<Tag size={16}/>}>Tags (Từ khóa)</Label>
                    <div className="flex flex-wrap gap-2 p-2 bg-gray-100 rounded-lg min-h-[40px]">
                        {tags.length > 0 ? (
                            tags.map(tag => (
                                <span key={tag} className="px-2.5 py-1 text-xs font-semibold text-white bg-[#2b8cee] rounded-full">
                                    {tag}
                                </span>
                            ))
                        ) : (
                            <span className="text-sm text-gray-500 px-2">Chờ AI phân tích...</span>
                        )}
                    </div>
                </div>
            </Card>
        </div>
      </div>
    </form>
  );
};

export default CreateQuestionWithAIPage;
